.descriptionTitle {
    font-size: 32px;
    font-weight: bold;
    padding-top: 20px;
    margin-bottom: 10px;
    text-align: center;
}

.websiteLink {
    color: #E8E8E8;
    font-size: 12px;
    text-align: center;
    text-decoration: underline;
}

.table {
    margin-bottom: 5px;
}

.rowsGigl {
    width: 180px;
    height: 50px;
    text-align: center;
}

.rowsArrToZone {
    width: 100px;
    height: 50px;
    text-align: center;
}

.rowsTrivia {
    width: 80px;
    height: 50px;
    text-align: center;
}

.logoImages {
    width: 100px;
}

.logoImagesArrToZone {
    width: 60px;
}

.underline {
    background-color: #E8E8E8;
    color: #404040;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin-top: 150px;
    filter: grayscale(60%);
    padding-bottom: 10px;
}

.projectOverview {
    font-size: 18px;
    text-align: center;
}

.projectHeader {
    font-size: 18px;
    font-weight: bold;
    text-align: center;
}

.techStack {
    font-size: 16px;
    font-weight: bold;
}

@media (min-width: 690px) {
    .demo {
        height: 390px;
    }
}

@media (max-width: 689px) {
    .demo {
        height: 300px;
    }
}

@media (min-width: 690px) {
    .demoArrToZone {
        height: 390px;
    }
}

@media (max-width: 689px) {
    .demoArrToZone {
        height: 220px;
        border: 4px solid #404040;
        margin-top: 8px; 
    }
}
 